import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Button from 'material-ui/Button';
import Dialog, {
    DialogActions,
    DialogContent,
    DialogTitle,
} from 'material-ui/Dialog';
import Checkbox from 'material-ui/Checkbox';
import PlaylistAddIcons from 'material-ui-icons/PlaylistAdd';
import PlaylistPlayIcons from 'material-ui-icons/PlaylistPlay';

import {
    setAppStateToLocalStorage,
    getAppStateToLocalStorage,
} from '../../utils/storage';

class Intro extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: true,
            checked: false,
        };
    }

    handleClose = () => {
        if (this.state.checked) {
            const appState = getAppStateToLocalStorage();
            appState.tutorial = false;
            setAppStateToLocalStorage(appState);
            console.log(appState)
        }
        this.setState({ open: false });
    };

    handleChecked = (e) => {
        this.setState({
            checked: !this.state.checked,
        })
    };

    render() {
        return (
            <Dialog
                onClose={this.handleClose}
                disableBackdropClick={true}
                disableEscapeKeyDown={true}
                aria-labelledby="simple-dialog-title"
                open={this.state.open}
            >
                <DialogTitle id="simple-dialog-title">   TOP 100   ,    !</DialogTitle>
                <DialogContent style={{
                    fontWeight:300,
                }}>
                    <ul>
                        <li>      . (      )</li>
                        <li>
                            <PlaylistAddIcons style={{ position:'relative', top: 6 }} />
                                   .</li>
                        <li>
                            <PlaylistPlayIcons style={{ position:'relative', top: 6 }} />
                                   .</li>
                        <li style={{ position:'relative', top: 6 }}>       .</li>
                    </ul>
                </DialogContent>
                <DialogActions>
                    <div>
                        <Checkbox
                            checked={this.state.checked}
                            onChange={this.handleChecked}
                            value="checked"
                        />
                        <span style={{ fontWeight: 300 }}>
                              
                        </span>
                    </div>
                    <Button onClick={this.handleClose} color="primary" autoFocus>
                        
                    </Button>
                </DialogActions>
            </Dialog>
        );
    }
}

export default Intro;



